<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-基础用法1" class="vue-doc-element">基础用法</h2>
<p class="vue-doc-element">null</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">级联选择对数据有较严格要求，请参照示例的格式使用<code class="vue-doc-element">data</code>，每项数据至少包含 <code class="vue-doc-element">value</code>、<code class="vue-doc-element">label</code> 两项，子集为 <code class="vue-doc-element">children</code>，以此类推。</p>
<p class="vue-doc-element"><code class="vue-doc-element">value</code> 为当前选择的数据的 <code class="vue-doc-element">value</code> 值的数组，比如 <code class="vue-doc-element">[&#39;beijing&#39;, &#39;gugong&#39;]</code> ，按照级联顺序依次排序，使用 <code class="vue-doc-element">v-model</code> 进行双向绑定。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Cascader :data="data" v-model="value1"></Cascader>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue">&lt;template&gt;
    &lt;Cascader :data=<span class="hljs-string">"data"</span> v-model=<span class="hljs-string">"value1"</span>&gt;&lt;/Cascader&gt;
&lt;/template&gt;
&lt;script&gt;
    export default {
        data () {
            return {
                value1: [],
                data: [{
                    value: <span class="hljs-string">'beijing'</span>,
                    <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'北京'</span>,
                    children: [
                        {
                            value: <span class="hljs-string">'gugong'</span>,
                            <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'故宫'</span>
                        },
                        {
                            value: <span class="hljs-string">'tiantan'</span>,
                            <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'天坛'</span>
                        },
                        {
                            value: <span class="hljs-string">'wangfujing'</span>,
                            <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'王府井'</span>
                        }
                    ]
                }, {
                    value: <span class="hljs-string">'jiangsu'</span>,
                    <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'江苏'</span>,
                    children: [
                        {
                            value: <span class="hljs-string">'nanjing'</span>,
                            <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'南京'</span>,
                            children: [
                                {
                                    value: <span class="hljs-string">'fuzimiao'</span>,
                                    <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'夫子庙'</span>,
                                }
                            ]
                        },
                        {
                            value: <span class="hljs-string">'suzhou'</span>,
                            <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'苏州'</span>,
                            children: [
                                {
                                    value: <span class="hljs-string">'zhuozhengyuan'</span>,
                                    <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'拙政园'</span>,
                                },
                                {
                                    value: <span class="hljs-string">'shizilin'</span>,
                                    <span class="hljs-selector-tag">label</span>: <span class="hljs-string">'狮子林'</span>,
                                }
                            ]
                        }
                    ],
                }]
            }
        }
    }
&lt;/script&gt;

</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                value1: [],
                data: [{
                    value: 'beijing',
                    label: '北京',
                    children: [
                        {
                            value: 'gugong',
                            label: '故宫'
                        },
                        {
                            value: 'tiantan',
                            label: '天坛'
                        },
                        {
                            value: 'wangfujing',
                            label: '王府井'
                        }
                    ]
                }, {
                    value: 'jiangsu',
                    label: '江苏',
                    children: [
                        {
                            value: 'nanjing',
                            label: '南京',
                            children: [
                                {
                                    value: 'fuzimiao',
                                    label: '夫子庙',
                                }
                            ]
                        },
                        {
                            value: 'suzhou',
                            label: '苏州',
                            children: [
                                {
                                    value: 'zhuozhengyuan',
                                    label: '拙政园',
                                },
                                {
                                    value: 'shizilin',
                                    label: '狮子林',
                                }
                            ]
                        }
                    ],
                }]
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


