<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-禁用1" class="vue-doc-element">禁用</h2>
<p class="vue-doc-element">null</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">设置属性 disabled 可以禁用组件。</p>
<p class="vue-doc-element">给某项数据设置 disabled: true 可以禁用某一项。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Cascader :data="data2" disabled></Cascader>
    <Cascader :data="data2"></Cascader>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Cascader</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Cascader</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Cascader</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Cascader</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">data2</span>: [{
                    <span class="hljs-attr">value</span>: <span class="hljs-string">'zhejiang'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'浙江'</span>,
                    <span class="hljs-attr">children</span>: [{
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'hangzhou'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'杭州'</span>,
                        <span class="hljs-attr">children</span>: [{
                            <span class="hljs-attr">value</span>: <span class="hljs-string">'xihu'</span>,
                            <span class="hljs-attr">label</span>: <span class="hljs-string">'西湖'</span>
                        }]
                    }]
                }, {
                    <span class="hljs-attr">value</span>: <span class="hljs-string">'jiangsu'</span>,
                    <span class="hljs-attr">label</span>: <span class="hljs-string">'江苏'</span>,
                    <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
                    <span class="hljs-attr">children</span>: [{
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'nanjing'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'南京'</span>,
                        <span class="hljs-attr">children</span>: [{
                            <span class="hljs-attr">value</span>: <span class="hljs-string">'zhonghuamen'</span>,
                            <span class="hljs-attr">label</span>: <span class="hljs-string">'中华门'</span>
                        }]
                    }]
                }]
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                data2: [{
                    value: 'zhejiang',
                    label: '浙江',
                    children: [{
                        value: 'hangzhou',
                        label: '杭州',
                        children: [{
                            value: 'xihu',
                            label: '西湖'
                        }]
                    }]
                }, {
                    value: 'jiangsu',
                    label: '江苏',
                    disabled: true,
                    children: [{
                        value: 'nanjing',
                        label: '南京',
                        children: [{
                            value: 'zhonghuamen',
                            label: '中华门'
                        }]
                    }]
                }]
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


