<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-默认值1">默认值</a></li><li><a href="#vue-doc-移入展开1">移入展开</a></li><li><a href="#vue-doc-自定义显示1">自定义显示</a></li><li><a href="#vue-doc-禁用1">禁用</a></li><li><a href="#vue-doc-选择即改变1">选择即改变</a></li><li><a href="#vue-doc-自定义已选项1">自定义已选项</a></li><li><a href="#vue-doc-尺寸1">尺寸</a></li><li><a href="#vue-doc-动态加载选项1">动态加载选项</a></li><li><a href="#vue-doc-搜索1">搜索</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-cascader级联选择1" class="vue-doc-element">Cascader 级联选择</h1>
<p class="vue-doc-element">从一组相关联的数据集合中进行选择，常用于省市区、公司级层、事务分类等。相比 Select 组件，可以一次性完成选择，体验更好。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demodefault></demodefault>
<demohover></demohover>
<democustom></democustom>
<demodisabled></demodisabled>
<demoselect></demoselect>
<democustomOptions></democustomOptions>
<demosize></demosize>
<demoloading></demoloading>
<demosearch></demosearch>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>可选项的数据源，格式参照示例说明</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>value</td>
<td>当前已选项的数据，格式参照示例说明</td>
<td>Array</td>
<td>[]</td>
</tr>
<tr>
<td>render-format</td>
<td>选择后展示的函数，用于自定义显示格式</td>
<td>Function</td>
<td>label =&gt; label.join(&#39; / &#39;)</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用选择器</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>clearable</td>
<td>是否支持清除</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>placeholder</td>
<td>输入框占位符</td>
<td>String</td>
<td>请选择</td>
</tr>
<tr>
<td>trigger</td>
<td>次级菜单展开方式，可选值为 click 或 hover</td>
<td>String</td>
<td>click</td>
</tr>
<tr>
<td>change-on-select</td>
<td>当此项为 true 时，点选每级菜单选项值都会发生变化，具体见上面的示例</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>输入框大小，可选值为large和small或者不填</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>load-data</td>
<td>动态获取数据，数据源需标识 loading</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>filterable</td>
<td>是否支持搜索</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>not-found-text</td>
<td>当搜索列表为空时显示的内容</td>
<td>String</td>
<td>无匹配数据</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内，在 Tabs、带有 fixed 的 Table 列内使用时，建议添加此属性，它将不受父级样式影响，从而达到更好的效果</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>element-id</td>
<td>给表单元素设置 id，详见 Form 用法。</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>选择完成后的回调，返回值 value 即已选值 value，selectedData 为已选项的具体数据</td>
<td>value, selectedData</td>
</tr>
<tr>
<td>on-visible-change</td>
<td>展开和关闭弹窗时触发</td>
<td>显示状态，Boolean</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demodefault from './demo/default.md';
import demohover from './demo/hover.md';
import democustom from './demo/custom.md';
import demodisabled from './demo/disabled.md';
import demoselect from './demo/select.md';
import democustomOptions from './demo/customOptions.md';
import demosize from './demo/size.md';
import demoloading from './demo/loading.md';
import demosearch from './demo/search.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demodefault,demohover,democustom,demodisabled,demoselect,democustomOptions,demosize,demoloading,demosearch
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


