<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-动态加载选项1" class="vue-doc-element">动态加载选项</h2>
<p class="vue-doc-element">null</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">使用 load-data 属性可以异步加载子选项，需要给数据增加 loading 来标识当前是否在加载中。</p>
<p class="vue-doc-element">load-data 的第二个参数为回调，如果执行，则会自动展开当前项的子列表。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Cascader :data="data4" :load-data="loadData"></Cascader>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Cascader</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data4"</span> <span class="hljs-attr">:load-data</span>=<span class="hljs-string">"loadData"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">Cascader</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">data4</span>: [
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'beijing'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'北京'</span>,
                        <span class="hljs-attr">children</span>: [],
                        <span class="hljs-attr">loading</span>: <span class="hljs-literal">false</span>
                    },
                    {
                        <span class="hljs-attr">value</span>: <span class="hljs-string">'hangzhou'</span>,
                        <span class="hljs-attr">label</span>: <span class="hljs-string">'杭州'</span>,
                        <span class="hljs-attr">children</span>: [],
                        <span class="hljs-attr">loading</span>:<span class="hljs-literal">false</span>
                    }
                ]
            }
        },
        <span class="hljs-attr">methods</span>: {
            loadData (item, callback) {
                item.loading = <span class="hljs-literal">true</span>;
                setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">if</span> (item.value === <span class="hljs-string">'beijing'</span>) {
                        item.children = [
                            {
                                <span class="hljs-attr">value</span>: <span class="hljs-string">'talkingdata'</span>,
                                <span class="hljs-attr">label</span>: <span class="hljs-string">'TalkingData'</span>
                            },
                            {
                                <span class="hljs-attr">value</span>: <span class="hljs-string">'baidu'</span>,
                                <span class="hljs-attr">label</span>: <span class="hljs-string">'百度'</span>
                            },
                            {
                                <span class="hljs-attr">value</span>: <span class="hljs-string">'sina'</span>,
                                <span class="hljs-attr">label</span>: <span class="hljs-string">'新浪'</span>
                            }
                        ];
                    } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (item.value === <span class="hljs-string">'hangzhou'</span>) {
                        item.children = [
                            {
                                <span class="hljs-attr">value</span>: <span class="hljs-string">'ali'</span>,
                                <span class="hljs-attr">label</span>: <span class="hljs-string">'阿里巴巴'</span>
                            },
                            {
                                <span class="hljs-attr">value</span>: <span class="hljs-string">'163'</span>,
                                <span class="hljs-attr">label</span>: <span class="hljs-string">'网易'</span>
                            }
                        ];
                    }
                    item.loading = <span class="hljs-literal">false</span>;
                    callback();
                }, <span class="hljs-number">1000</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                data4: [
                    {
                        value: 'beijing',
                        label: '北京',
                        children: [],
                        loading: false
                    },
                    {
                        value: 'hangzhou',
                        label: '杭州',
                        children: [],
                        loading:false
                    }
                ]
            }
        },
        methods: {
            loadData (item, callback) {
                item.loading = true;
                setTimeout(() => {
                    if (item.value === 'beijing') {
                        item.children = [
                            {
                                value: 'talkingdata',
                                label: 'TalkingData'
                            },
                            {
                                value: 'baidu',
                                label: '百度'
                            },
                            {
                                value: 'sina',
                                label: '新浪'
                            }
                        ];
                    } else if (item.value === 'hangzhou') {
                        item.children = [
                            {
                                value: 'ali',
                                label: '阿里巴巴'
                            },
                            {
                                value: '163',
                                label: '网易'
                            }
                        ];
                    }
                    item.loading = false;
                    callback();
                }, 1000);
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


